.SearchForm {
  margin: 5%;
  padding: 15px;
}

.home-search {
  padding: 15px;
  border: 1px solid #f1f1f1;
  background: white;
  border-radius: 5px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;

}

.home-search label {
  display: flex;
  flex-direction: row;
  width: 100%;
}

#search {
  display: flex;
  flex-direction: row;
  border: none;
  width: 90%;
}

#search-submit {
  display: flex;
  flex-direction: row;
  margin: 0 0 0 auto;
  background: black;
  color: #fff;
  /* width: 15%; */
  padding: 20px;
  border-radius: 5px;;
}

#search-submit:hover {
background: grey;
transition: .3s;
}

